      loading: false,
      finished: false,
      page: 1,
      // scrollTop: 0, //content æ»šåŠ¨é«˜åº¦ï¼ˆè·å–scrollTopæ–¹æ¡ˆä¸€ï¼‰

    };
  },
  computed: {
    ...mapState(['goodList', 'isFinished', 'counterMap']),

  },
  methods: {
    ...mapMutations(['resetGoodList', 'setGoodList']),
    ...mapActions(['getGoodList']),
    changeType(type) {
      if (type === 'all') this.type = 'all';
      else if (type === 'sale') this.type = 'sale';
      else if (this.type === 'price-up') this.type = 'price-down';
      else this.type = 'price-up';
      this.onRefresh('refresh');
    },
    // ä¸‹æ‹‰åˆ·æ–°
    async onRefresh() {
      this.refreshing = true;

      // æ¸…ç©ºåˆ—è¡¨
      this.resetGoodList();

      // é‡æ–°åŠ è½½æ•°æ®
      this.finished = false;
      this.onLoad('refresh');
    },
    // ä¸Šæ‹‰åŠ è½½ ï¼ˆè¿›è¡Œç¿»é¡µåŠ è½½: å°±æ˜¯ä¸‹ä¸€é¡µ/è·å–ä¸‹ä¸€é¡µæ•°æ®ï¼‰
    async onLoad(refresh) {
      this.finished = false;
      this.page += 1;
      if (refresh === 'refresh') this.page = 1;
      this.loading = true;// æ•°æ®åŠ è½½ä¸­

      const hasMoreData = await this.getGoodList({
        page: this.page,
        sort: this.type,
      });
      this.loading = false;
      this.refreshing = false;
      console.log('hasMoreData', hasMoreData);
      if (!hasMoreData) {
        this.finished = true;// æ‰€æœ‰æ•°æ®åŠ è½½å®Œäº†ï¼Œ ä¸ä¼šå†è§¦å‘loadäº‹ä»¶
      }
    },
  },

  watch: {
    // åˆ‡æ¢å­ç±»ç›®æ—¶ï¼Œåˆå§‹åŒ–æ•°æ®ã€‚
    isFinished() {
      console.log('reset fini--------------------------');
      this.finished = false;
      this.loading = false;
      this.refreshing = false;
      this.page = 1;
    },
  },
  mounted() {
    // è·å–scrollTop æ–¹æ¡ˆä¸€ ï¼š ç»™domæ³¨å†Œscrolläº‹ä»¶ï¼ˆç»‘å®šä¸€ä¸ªscrolläº‹ä»¶å¤„ç†å‡½æ•°ï¼‰
    // console.log('mounted');
    // this.content = this.$refs.content;
    // console.dir(this.content);
    // this.content.addEventListener('scroll', () => {
    //   console.log('scroll');
    //   this.scrollTop = this.content.scrollTop; // å¯ä»¥æ‹¿åˆ°scrollTop
    //   console.log(this.scrollTop);
    // });
    // è·å–scrollTop æ–¹æ¡ˆäºŒ ï¼š å€Ÿç”¨å¯¼èˆªå®ˆå« ï¼Œ è¯¦è§ router/index.js
  },
  // æ–¹æ¡ˆä¸‰ï¼šè¡Œä¸é€š
  // deactivated() {
  // console.log('deactivated');
  // console.dir(this.$refs.content); //scrollTop å§‹ç»ˆä¸º0 ï¼Œæ‹¿ä¸åˆ°scrollTop
  // console.log('scrollTop', this.$refs.content.scrollTop); // æ‹¿ä¸åˆ°scrollTop
  // this.scrollTop = this.$refs.content.scrollTop;
  // console.log(this.scrollTop);
  // },
  activated() {
    const { scrollTop } = this.$route.meta;
