        top: scrollTop, // å½“å‰é‡‡ç”¨æ–¹æ¡ˆäºŒï¼Œ  å¦‚æœæ˜¯ï¼Œæ–¹æ¡ˆä¸€ï¼Œåªéœ€å°†æ­¤å¤„scrollTop æ”¹å†™æˆthis.scrollTop
      });
    }
  },
};
</script>

